import React, { Component } from 'react'
//iconfont.cn 官网搜选图标，加入购物车
//点击页面右上角购物车，将图标添加进项目（没有项目就先点击“下载到本地”）
//解压压缩包，全部复制到项目public文件夹下，在index.html中通过link引入iconfont.css
//使用时，<i className = 'iconfont icon-具体名字'></i>
import { Carousel, WingBlank } from 'antd-mobile';
import Mytab from './Mytab'
import './home.css'

class Home extends Component {
    state = {
        data: ['1', '2', '3','4','5'],
        imgHeight: 176,
    }
    componentDidMount() {
        // simulate img loading
        setTimeout(() => {
            this.setState({
                data: ['0', '1', '2','3','4'],
            });
        }, 100);
    }
    render() {
        return (
            <div className="big">
                <div className="title">
                    <img className="taobao" src="image/taobao.png" />
                    {/* <i className="tao"></i> */}
                    <input type="text" placeholder="寻找宝贝店铺" onClick={() => this.props.history.push('/search')} style={{ marginLeft: "50px", width: "300px", marginTop: "5px", backgroundColor: "#ff4e22" }} />
                </div>

                <WingBlank>
                    <Carousel
                        autoplay
                        infinite
                        beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                        afterChange={index => console.log('slide to', index)}
                    >
                        {this.state.data.map(val => (
                            <a
                                key={val}
                                href="http://www.alipay.com"
                                style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                            >
                                <img
                                    src={`image/${val}.jpg`}
                                    alt=""
                                    style={{ width: '100%', verticalAlign: 'top' }}
                                    onLoad={() => {
                                        // fire window resize event to change height
                                        window.dispatchEvent(new Event('resize'));
                                        this.setState({ imgHeight: 'auto' });
                                    }}
                                />
                            </a>
                        ))}
                    </Carousel>
                </WingBlank>
                <div className="home">
                    <div className="column" onClick={() => this.props.history.push('/book')}>
                        <img className="home1" src="image/book.jpg"/>
                        <p className="bookmark">书名：React进阶之路</p>
                        <p className="bookmark">价格:60元</p>
                    </div>
                    <div className="column" onClick={() => this.props.history.push('/book1')}>
                        <img className="home1" src="image/book1.jpg"/>
                        <p className="bookmark">书名：React进阶之路</p>
                        <p className="bookmark">价格:60元</p>
                    </div>
                    <div className="column" onClick={() => this.props.history.push('/book2')}>
                        <img className="home1" src="image/book2.jpg"/>
                        <p className="bookmark">书名：React进阶之路</p>
                        <p className="bookmark">价格:60元</p>
                    </div>
                    <div className="column" onClick={() => this.props.history.push('/book3')}>
                        <img className="home1" src="image/book3.jpg"/>
                        <p className="bookmark">书名：React进阶之路</p>
                        <p className="bookmark">价格:60元</p>
                    </div>
                    <div className="column" onClick={() => this.props.history.push('/book4')}>
                        <img className="home1" src="image/book4.jpg"/>
                        <p className="bookmark">书名：React进阶之路</p>
                        <p className="bookmark">价格:60元</p>
                    </div>
                    <div className="column" onClick={() => this.props.history.push('/book5')}>
                        <img className="home1" src="image/book5.jpg" />
                        <p className="bookmark">书名：React进阶之路</p>
                        <p className="bookmark">价格:60元</p>
                    </div>
                    <div className="column" onClick={() => this.props.history.push('/book6')}>
                        <img className="home1" src="image/book6.jpg"/>
                        <p className="bookmark">书名：React进阶之路</p>
                        <p className="bookmark">价格:60元</p>
                    </div>
                    <div className="column" onClick={() => this.props.history.push('/book7')}>
                        <img className="home1" src="image/book7.jpg"/>
                        <p className="bookmark">书名：React进阶之路</p>
                        <p className="bookmark">价格:60元</p>
                    </div>
                    <div className="column" onClick={() => this.props.history.push('/book8')}>
                        <img className="home1" src="image/book8.jpg"/>
                        <p className="bookmark">书名：React进阶之路</p>
                        <p className="bookmark">价格:60元</p>
                    </div>
                    <div className="column" onClick={() => this.props.history.push('/book9')}>
                        <img className="home1" src="image/book9.jpg"/>
                        <p className="bookmark">书名：React进阶之路</p>
                        <p className="bookmark">价格:60元</p>
                    </div>    
                </div>
                <Mytab />
            </div>
        );
    }
}
export default Home
